// root ,    
import React from "react";
import AppLayout from "../components/AppLayout";
import PropTypes from "prop-types";
import reducer from "../reducers"; 
import { Provider } from 'react-redux'; //   ()
import createSagaMiddleware from 'redux-saga';
import withReduxSaga from 'next-redux-saga';
import withRedux from 'next-redux-wrapper'
import { createStore, applyMiddleware, compose} from 'redux';
import rootSaga from "../sagas";
import { LOAD_USER_REQUEST } from "../reducers/user";
import axios from "axios";
import Helmet from 'react-helmet';
import App, { Container } from 'next/app'; // _document.js  renderpage      ( head  )
const Portfolio = ({ Component, store, pageProps }) => {
  return (
    // Provider          
    //  state, action, reducer   
    <Container>
     <Provider store={store}>
      <Helmet
        title="DSS PortFolio"
        htmlAttributes={{ lang: 'ko'}}
        meta={[{
          charSet:'UTF-8',
        },{
          name:'viewport', 
          content: 'width=device-width,initial-scale=1.0,minimum-scale=1.0,maximum-scale=1.0,user-scalable=yes,viewport-fit=cover',
        }, {
          'http-equiv': 'X-UA-Compatible', content: 'IE=edge',
        }, {
          name: 'description', content: 'DSS PortFolio',
        }, {
          name: 'og:title', content: 'NodeBird',
        }, {
          name: 'og:description', content: 'DSS PortFolio',
        }, {
          property: 'og:type', content: 'website',
        }, {
          property:'og:image', content: 'http://localhost:3060/favicon.ico',
        }]}
        link={[{
          rel: 'shortcut icon', href: '/favicon.ico',
        }, {
          rel: 'stylesheet', href: 'https://cdnjs.cloudflare.com/ajax/libs/antd/3.26.11/antd.css',
        }, {
          rel: 'stylesheet', href: 'https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick.min.css',
        }, {
          rel: 'stylesheet', href: 'https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick-theme.min.css',
        }]}
      >
      </Helmet>
        <AppLayout>
          <Component {...pageProps}></Component>
        </AppLayout>
    </Provider>
   </Container>
  );
};

Portfolio.propTypes = {
  Component: PropTypes.elementType.isRequired, // JSX   (,,, )
  store: PropTypes.object.isRequired, // isRequired : (  props ,   )
  pageProps : PropTypes.object.isRequired,
};

Portfolio.getInitialProps = async (context) =>{
  console.log(context);
  const { ctx, Component } = context;
  let pageProps = {};
  const state = ctx.store.getState();// AppLayout  SSR 
  //      .
  const cookie = ctx.isServer ? ctx.req.headers.cookie : '';// >     (withCridentials:true),
  axios.defaults.headers.Cookie = ''; 
  if(ctx.isServer && cookie){ // (SSR)    
    axios.defaults.headers.Cookie = cookie; // SSR   ,  axios  (  )  
  }
  if(!state.user.me){ // AppLayout  !me     -  me  
    ctx.store.dispatch({
      type: LOAD_USER_REQUEST,
    });
  }
  if(Component.getInitialProps){
    pageProps = await Component.getInitialProps(ctx) || {};  // : 1. server , 2. page getInit, 3.  (ctx)
  }
  return { pageProps };
};

const configureStore = (initialState, options) => {
  const sagaMiddleware = createSagaMiddleware();
  const middlewares = [sagaMiddleware , (store)=>(next)=>(action)=>{ //    -  
    // console.log(action);
    next(action);
  }];
  //       
  const enhancer = process.env.NODE_ENV === 'production' ? compose(applyMiddleware(...middlewares),) : compose(applyMiddleware(...middlewares), !options.isServer && typeof window !== 'undefined' && window.__REDUX_DEVTOOLS_EXTENSION__ !== 'undefined' ? window.__REDUX_DEVTOOLS_EXTENSION__() : (f) => f, ) 
  const store = createStore(reducer, initialState, enhancer);
  //  store 
  store.sagaTask = sagaMiddleware.run(rootSaga); //SSR 
  return store;
}

export default withRedux(configureStore)(withReduxSaga(Portfolio));//  ( -  )
