import React, { useState, useCallback, useEffect } from "react";
import { Form, Input, Checkbox, Button } from "antd";
import { SIGN_UP_REQUEST , isSigningUp } from "../reducers/user";
import { useDispatch, useSelector } from "react-redux";
import Router from 'next/router';
import styled from 'styled-components';

const SignupError = styled.div`
  color: red;
`;
// Coustom hook      (   )
// useState(hook)       
export const userInput = (initValue = null) => {
  const [value, setter] = useState(initValue);
  const handler = useCallback(e => {
    setter(e.target.value);
  }, []);
  return [value, handler];
};

////////////////////////////////

const Signup = () => {
  const [id, setId] = useState("");
  const [nick, setNick] = useState("");
  const [password, setPassword] = useState("");
  const [passwordCheck, setPasswordCheck] = useState("");
  const [term, setTerm] = useState(false);
  const [passwrodError, setpasswordError] = useState(false);
  const [termError, setTermError] = useState(false);
  const dispatch = useDispatch()
  const { isSigningUp, me } = useSelector(state =>state.user)

  useEffect(()=>{
    if(me){
      alert(' ');
      Router.push('/');
    }
  },[me && me.id])
  const onSubmit = useCallback(
    e => {
      e.preventDefault();
      console.log({
        id,
        nick,
        password,
        passwordCheck,
        term
      });
      if (password !== passwordCheck) {
        return setpasswordError(true);
      }
      if (!term) {
        return setTermError(true);
      }
      return dispatch({
        type: SIGN_UP_REQUEST,
        data:{
          userId: id,
          password,
          nickname: nick,
        },
      });
    }, [id, nick, password, passwordCheck, term]  // usecallback    state  
  );
  const onChangeId = e => {
    setId(e.target.value);
  };
  const onChangeNick = e => {
    setNick(e.target.value);
  };
  const onChangePassword = e => {
    setPassword(e.target.value);
  };
  const onChangePasswordChk = useCallback(
    e => {
      setpasswordError(e.target.value !== password);
      setPasswordCheck(e.target.value);
    },
    [password]
  );
  const onChangeTerm = useCallback(e => {
    setTermError(false);
    setTerm(e.target.checked);
  }, []);
  if(me){
    return null;
  }

  return (
    <>
      <Form onSubmit={onSubmit} style={{ padding: 10 }}>
        <div>
          <label htmlFor="user-id"></label>
          <br />
          <Input
            name="user-id"
            required
            value={id}
            onChange={onChangeId}
          ></Input>
        </div>

        <div>
          <label htmlFor="user-nick"></label>
          <Input
            name="user-nick"
            required
            value={nick}
            onChange={onChangeNick}
          ></Input>
        </div>
        <div>
          <label htmlFor="user-pass"></label>
          <br />

          <Input
            name="user-pass"
            type="password"
            required
            value={password}
            onChange={onChangePassword}
          ></Input>
        </div>
        <div>
          <label htmlFor="user-pass-chk"> </label>
          <br />

          <Input
            name="user-pass-chk"
            type="password"
            required
            value={passwordCheck}
            onChange={onChangePasswordChk}
          ></Input>
          {passwrodError && (
            <SignupError>   .</SignupError>
          )}
        </div>
        <div>
          <Checkbox name="user-term" value={term} onChange={onChangeTerm}>
            .
          </Checkbox>
          {termError && (
            <SignupError>   .</SignupError>
          )}
        </div>
        <div>
          <div style={{ marginTop: 10 }}></div>
          <Button type="primary" htmlType="submit" loading={isSigningUp}>
            
          </Button>
        </div>
      </Form>
    </>
  );
};
export default Signup;
